/*
 * Globals
 */
/* Links */
a,
a:focus,
a:hover {
  color: #007bff;
}

/* Custom default button */
.btn-secondary,
.btn-secondary:hover,
.btn-secondary:focus {
  color: #212529;
  text-shadow: none;
  /* Prevent inheritance from `body` */
  background-color: #fff;
  border: 0.8px solid #373a3c;
  border: 0.05rem solid #373a3c;
}

/*
 * Base structure
 */
html,
body {
  height: 100%;
  background-color: #fff;
}

body {
  display: -ms-flexbox;
  display: flex;
  color: #373a3c;
  background: #fff;
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, .5);
}

