.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  min-height: 100vh;
  padding: 16px;
  padding: 1rem;
  margin: auto;
}

.nav .nav-link {
  padding: 4px 0;
  padding: .25rem 0;
  font-weight: 700;
  background-color: transparent;
  border-bottom: 4px solid transparent;
  border-bottom: .25rem solid transparent;
}

.nav .nav-link:hover,
.nav .nav-link:focus {
  border-bottom-color: #ff4f00;
}

.nav .nav-link + .nav-link {
  margin-left: 16px;
  margin-left: 1rem;
}

.nav .active {
  color: #5c2040;
  border-bottom-color: #5c2040;
}

.app-content {
  -ms-flex: 1;
      flex: 1;
  padding: 16px 0;
  padding: 1rem 0;
}

.app-content .btn-sm {
  padding: 12px 20px;
  padding: .75rem 1.25rem;
  font-weight: 700;
}

.app-footer {
  padding: 16px 0;
  padding: 1rem 0;
  font-size: 12.8px;
  font-size: .8rem;
  color: #a0a0a0;
  background: transparent;
}

.app-footer a {
  color: #a0a0a0;
}

.app-footer p {
  margin-bottom: 0;
}
